.modal {
  top: center;
  left: center;
  width: 480px;
  z-index: 300;
  opacity: 0;
  visibility: hidden;
  background-color: #2d2f2f;
  border-radius: 3px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
  transition: all 0.3s;
}
.modalIsShow {
  opacity: 1;
  visibility: visible;
}
.modalWide {
  width: 768px;
}
.modalMiddle {
  box: horizontal middle left;
  position: relative;
  border-top: 1px solid rgba(255,255,255,0.02);
  border-bottom: 1px solid rgba(0,0,0,0.1);
}
.modalMiddleScroll {
  box: horizontal top space-between;
  max-height: 200px;
  overflow: hidden;
  border-top: 0;
  border-bottom: 0;
}
.modalMiddleScroll:before {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 14px;
  z-index: 310;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.1) 0, rgba(0,0,0,0) 100%);
  content: '';
}
.modalMiddleScroll:after {
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 14px;
  z-index: 310;
  background-image: linear-gradient(to top, rgba(0,0,0,0.1) 0, rgba(0,0,0,0) 100%);
  content: '';
}
.modalMiddleScrollLarge {
  max-height: 70vh;
}
.modalMiddleScrollCart {
  max-height: calc(90vh - 300px);
}
.modalScrollRail {
  flex: 1 1 14px;
  position: relative;
  width: 14px;
  min-width: 14px;
  border-right: 1px solid rgba(0,0,0,0.1);
  border-left: 1px solid rgba(255,255,255,0.02);
  align-self: stretch;
}
.modalScrollBar {
  position: absolute;
  top: 30px;
  left: 2px;
  width: 8px;
  height: 100px;
  background-color: #353939;
  border-radius: 4px;
}
