.modalTitle {
  box: hortizontal middle space-between;
  position: relative;
  min-height: 77px;
  z-index: 320;
  padding: 15px 30px;
  border-bottom: 1px solid rgba(0,0,0,0.1);
}
.modalHeader {
  box: horizontal middle left;
  padding-right: 40px;
  flex-wrap: wrap;
}
.modalHeader h6 {
  margin-right: 13px;
  color: #8eabab;
  font-family: 'Circe', sans-serif;
  font-size: 30px;
  font-weight: 400;
}
.modalHeaderCenter {
  box: horizontal middle center;
  flex: 1 1 auto;
  padding-right: 0;
  padding-left: 20px;
}
.modalHeaderTag {
  box: vertical middle center;
  height: 28px;
  margin-right: 13px;
  padding: 2px 10px 0;
  padding-top: 2px;
  background-color: #282a2a;
  border-radius: 6px;
  color: #435050;
  font-family: 'Circe', sans-serif;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}
.modalClose {
  box: horizontal middle center;
}
.modalCloseButton {
  display: block;
  background-color: transparent;
  background-image: url("../img/modal/close.png");
  border: 0;
  outline: 0;
  transition: all 0.4s;
  size: 20px;
}
.modalCloseButton:hover {
  background-image: url("../img/modal/close-hover.png");
}
