.buttonLight {
  height: 40px;
  min-width: 112px;
  padding: 0 46px;
  padding-top: 2px;
  background-color: #3c4544;
  border: 0;
  border-radius: 6px;
  color: #8eabab;
  font-family: 'Circe', sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-transform: uppercase;
  text-decoration: none;
  outline: 0;
  transition: all 0.3s;
}
.buttonLight:hover {
  background-color: #485554;
  color: #fff;
}
.buttonLight:active {
  box-shadow: inset 0 1px 1px rgba(0,0,0,0.35);
}
.buttonDark {
  display: inline-block;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  height: 40px;
  max-width: 112px;
  padding: 0 46px;
  padding-top: 2px;
  background-color: #343a39;
  border: 0;
  border-radius: 6px;
  color: #546161;
  font-family: 'Circe', sans-serif;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  outline: 0;
  transition: all 0.4s;
  align-items: center;
}
.buttonDark:hover {
  background-color: #3f4846;
  color: #6f8888;
}
