.cart {
  display: block;
}
.cartOffer {
  padding: 12px 12px 12px;
  border-bottom: 1px solid rgba(0,0,0,0.1);
}
.cartOfferSlider {
  box: horizontal middle space-between;
}
.cartOfferSliderLeft {
  display: block;
  margin-right: 10px;
  background-image: url("../img/cart/arrow-left.png");
  background-repeat: no-repeat;
  transition: all 0.4s;
  size: 15px;
  background-position: center center;
}
.cartOfferSliderLeft:hover {
  background-image: url("../img/cart/arrow-left-hover.png");
}
.cartOfferSliderRight {
  display: block;
  margin-left: 10px;
  background-image: url("../img/cart/arrow-right.png");
  background-repeat: no-repeat;
  transition: all 0.4s;
  size: 15px;
  background-position: center center;
}
.cartOfferSliderRight:hover {
  background-image: url("../img/cart/arrow-right-hover.png");
}
.cartOfferSliderSlideWrap {
  box: horizontal middle center;
  flex: 1 1 auto;
  height: 26px;
  background-color: #282a2a;
  border-radius: 6px;
}
.cartOfferSliderSlide {
  color: #505c5b;
  font-family: 'Circe', sans-serif;
  font-size: 13px;
  font-weight: 400;
}
.cartOfferSliderSlide span {
  margin-left: 4px;
  color: #8eabab;
  font-size: 12px;
  text-transform: uppercase;
}
.cartTotals {
  box: vertical top left;
  margin-bottom: 20px;
  padding: 18px 21px;
  border-top: 1px solid rgba(255,255,255,0.02);
}
.cartTotalSubtotal {
  box: horizontal middle space-between;
  width: 100%;
  padding-right: 31px;
}
.cartTotalSubtotalTitle {
  color: #546161;
  font-family: 'Circe', sans-serif;
  font-size: 14px;
  font-weight: 400;
}
.cartTotalSubtotalValue {
  display: inline-block;
  position: relative;
  color: #505c5b;
  font-family: 'Circe', sans-serif;
  font-size: 13px;
  font-size: 11px;
  font-weight: 700;
}
.cartTotalSubtotalValue span {
  font-weight: 400;
}
.cartTotalSubtotalValue:after {
  display: block;
  position: absolute;
  top: calc(50% - 1px);
  left: -2px;
  background-color: rgba(115,134,134,0.7);
  content: '';
  size: calc(100% + 4px) 1px;
}
.cartTotal {
  box: horizontal middle space-between;
  width: 100%;
  padding-right: 31px;
}
.cartTotalTitle {
  color: #546161;
  font-family: 'Circe', sans-serif;
  font-size: 14px;
  font-weight: 400;
}
.cartTotalValue {
  color: #fff;
  font-family: 'Circe', sans-serif;
  font-size: 18px;
  font-size: 16px;
  font-weight: 700;
  font-weight: 400;
}
.cartTerms {
  box: horizontal middle space-between;
  padding: 18px 21px;
}
.cartTerms label {
  position: relative;
  padding-right: 20px;
  color: #546161;
  font-family: 'Circe', sans-serif;
  font-size: 14px;
  font-weight: 400;
  cursor: default;
}
.cartTerms label a {
  color: #546161;
}
.cartTerms label:after {
  position: absolute;
  top: center;
  right: -5px;
  content: '';
  border: 2px solid #3c4544;
  size: 20px;
}
.cartTerms input[type=checkbox]:checked + label:before {
  position: absolute;
  top: center;
  right: -5px;
  background-image: url("../img/cart/checkbox.png");
  background-repeat: no-repeat;
  content: '';
  border: 2px solid transparent;
  background-position: center center;
  size: 20px;
}
.cartTerms input {
  visibility: hidden;
}
.cartButtonAdd {
  padding: 0 22px 20px;
}
.cartButton {
  width: 100%;
  height: 41px;
  padding: 0 32px 0 65px;
  background-color: #3c4544;
  background-image: url("../img/cart/cart.png");
  background-repeat: no-repeat;
  border: 0;
  border-radius: 3px;
  color: #9bb8b8;
  font-family: 'Circe', sans-serif;
  font-size: 11px;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  outline: 0;
  transition: all 0.4s;
  white-space: nowrap;
  background-position: left 28px center;
}
.cartButton:hover {
  background-color: #44504f;
  background-image: url("../img/cart/cart-hover.png");
  color: #fff;
}
.cartButton:active {
  box-shadow: inset 0 1px 1px rgba(0,0,0,0.35);
}
.modalCartClearButton {
  display: block;
  margin-right: 10px;
  background-color: transparent;
  background-image: url("../img/cart/icon-cart.png");
  background-repeat: no-repeat;
  border: 0;
  outline: 0;
  transition: all 0.4s;
  size: 20px;
}
