.windowControl {
  box: horizontal middle right;
  position: absolute;
  right: 16px;
  top: 13px;
}
.windowControlTemplate,
.windowControlBack,
.windowControlHelp,
.windowControlZoom,
.windowControlClose {
  width: 18px;
  height: 18px;
  border-radius: 3px;
  background-color: #282a2a;
  margin-left: 3px;
  position: relative;
  z-index: 5;
  background-repeat: no-repeat;
  background-position: center center;
  transition: all 0.3s;
}
.windowControlTemplate:hover .windowControlTooltip,
.windowControlBack:hover .windowControlTooltip,
.windowControlHelp:hover .windowControlTooltip,
.windowControlZoom:hover .windowControlTooltip,
.windowControlClose:hover .windowControlTooltip {
  opacity: 1;
}
.windowControlTemplate .windowControlTooltip,
.windowControlBack .windowControlTooltip,
.windowControlHelp .windowControlTooltip,
.windowControlZoom .windowControlTooltip,
.windowControlClose .windowControlTooltip {
  position: absolute;
  display: inline-block;
  box-shadow: 0 1px 1px rgba(0,0,0,0.1);
  border-radius: 3px;
  background-color: #3c4141;
  height: 33px;
  padding: 0 17px;
  box: vertical middle center;
  color: #aebaba;
  font-family: 'Circe', sans-serif;
  font-size: 14px;
  font-weight: 400;
  top: 24px;
  left: 0;
  transform: translateX(calc(-50% + 9px));
  opacity: 0;
  transition: opacity 0.3s;
  z-index: 10;
  pointer-events: none;
}
.windowControlTemplate .windowControlTooltip:after,
.windowControlBack .windowControlTooltip:after,
.windowControlHelp .windowControlTooltip:after,
.windowControlZoom .windowControlTooltip:after,
.windowControlClose .windowControlTooltip:after {
  display: block;
  position: absolute;
  content: '';
  top: -7px;
  triangle: right-iso pointing-up;
  height: 7px;
  background-color: #3c4141;
  left: calc(50% - 7px);
}
.windowControlBack {
  background-image: url("../img/window/control/back.png");
}
.windowControlBack:hover {
  background-image: url("../img/window/control/back-hover.png");
}
.windowControlHelp {
  background-image: url("../img/window/control/help.png");
}
.windowControlHelp:hover {
  background-image: url("../img/window/control/help-hover.png");
}
.windowControlZoom {
  background-image: url("../img/window/control/zoom.png");
}
.windowControlZoom:hover {
  background-image: url("../img/window/control/zoom-hover.png");
}
.windowControlClose {
  background-image: url("../img/window/control/x-inact.png");
}
.windowControlClose:hover {
  background-image: url("../img/window/control/x-hover.png");
}
.windowControlClose .windowControlTooltip {
  transform: translateX(calc(-100% + 25px));
}
.windowControlClose .windowControlTooltip:after {
  left: calc(100% - 23px);
}
