.modalFooter {
  box: horizontal middle center;
  height: 77px;
  border-top: 1px solid rgba(255,255,255,0.02);
}
.modalFooter button {
  margin-right: 30px;
}
.modalFooter button:last-child {
  margin-right: 0;
}
.modalFooterCart {
  height: auto;
}
.modalFooterCart .cartTotals {
  margin-bottom: 0;
  border-top: 0;
  border-bottom: 1px solid rgba(0,0,0,0.1);
}
.modalFooterCart .cartButtonAdd {
  border-top: 1px solid rgba(255,255,255,0.02);
}
