.shares {
  box: vertical top;
  padding: 24px 0 0;
  border-bottom: 1px solid rgba(255,255,255,0.02);
  position: relative;
}
.shares:after {
  display: block;
  position: absolute;
  content: '';
  top: 0px;
  left: 0;
  width: 100%;
  height: 12px;
  background-color: rgba(110,116,116,0);
  background-image: linear-gradient(180deg, #000 0%, rgba(0,0,0,0) 90%, rgba(0,0,0,0) 100%);
  opacity: 0.08;
}
.shareItems {
  box: horizontal middle space-between;
  padding: 0 26px 10px;
  border-bottom: 1px solid rgba(255,255,255,0.02);
}
.shareItem {
  flex: 0 1 auto;
  text-decoration: none;
  box: vertical top center;
}
.shareItem:hover .shareItemTitle {
  color: #fff;
}
.shareItem:hover .shareItemIconCopy {
  background-image: url("../img/share/copy-hover.png");
}
.shareItem:hover .shareItemIconTW {
  background-image: url("../img/share/tw-hover.png");
}
.shareItem:hover .shareItemIconFB {
  background-image: url("../img/share/fb-hover.png");
}
.shareItemIcon {
  position: relative;
  size: 30px;
  background-repeat: no-repeat;
  background-position: center center;
  margin-bottom: 8px;
  transition: all 0.4s;
}
.shareItemIconCopy {
  background-image: url("../img/share/copy.png");
}
.shareItemIconTW {
  background-image: url("../img/share/tw.png");
}
.shareItemIconFB {
  background-image: url("../img/share/fb.png");
}
.shareItemTitle {
  color: #aebaba;
  font-family: 'Circe', sans-serif;
  font-size: 13px;
  font-weight: 400;
  transition: all 0.4s;
  text-align: center;
}
.shareBottom {
  border-top: 1px solid rgba(0,0,0,0.1);
  padding: 10px 12px 10px;
}
.shareWindow {
  display: block;
  width: 100%;
  height: 27px;
  box: horizontal middle center;
  border-radius: 2px;
  background-color: #333535;
  font-family: 'Circe', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #6d7474;
}
.shareWindow span {
  color: #aebaba;
}
