.modalContent {
  width: 100%;
  min-height: 150px;
  padding: 26px 93px 26px 28px;
}
.modalContent h4 {
  margin-bottom: 80px;
  color: #fff;
  font-family: 'Circe', sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
}
.modalContent p {
  margin-bottom: 20px;
  color: #fff;
  font-family: 'Circe', sans-serif;
  font-size: 16px;
  font-weight: 400;
}
.modalContent ul {
  padding-left: 13px;
  list-style: disc;
  color: #738686;
  font-family: 'Circe', sans-serif;
  font-size: 14px;
  font-weight: 400;
}
.modalContent ul li {
  margin-bottom: 10px;
}
.modalContentCart {
  padding: 27px 13px;
}
.modalContentWide {
  padding: 26px 28px;
}
.modalContentCenter {
  box: vertical top center;
  padding: 26px 0;
}
.modalProgress {
  position: relative;
  width: 60%;
}
.modalProgressRail {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #3c4444;
  size: 100% 1px;
}
.modalProgressBar {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 1px;
  background-color: #aebaba;
}
.modalProgressLegend {
  box: horizontal middle space-between;
  padding-top: 10px;
  color: #4e5858;
  font-family: 'Circe', sans-serif;
  font-size: 14px;
  font-weight: 400;
}
